<template>
  <div>
    <div>
      <el-tag type="info" effect="dark"></el-tag>
      <el-input placeholder="" v-model="tro" clearable />
      <el-button type="primary" @click="addtro"></el-button>
    </div>
    <el-divider></el-divider>
    <el-tag type="info" effect="dark"></el-tag>
    <el-form ref="form" :model="form" label-width="50px">
      <el-form-item label="">
        <el-input v-model="form.addr" placeholder="xyz.com"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="form.port"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input
          v-model="form.password"
          placeholder=" @ "
        ></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input type="textarea" v-model="form.name"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="add"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import Trojan from "../utils/trojan"
import { Now } from "../utils/time"
export default {
  data() {
    return {
      form: {
        addr: "",
        port: 443,
        password: "",
        name: ""
      },
      tro: ""
    }
  },
  methods: {
    add() {
      let form = this.form
      let data = {
        password: form.password,
        ip: "",
        port: parseInt(form.port),
        allow: 1,
        addr: form.addr,
        name: form.name
        // ping: 0
      }
      if (
        form.addr &&
        typeof form.addr == "string" &&
        form.port &&
        typeof form.port == "number" &&
        form.name &&
        form.password
      )
        this.addnode(data)
      else this.$message("!")
    },
    addtro() {
      let data = Trojan.toTrojan(this.tro)
      this.addnode(data)
    },
    addnode(data) {
      ipc.send("add-node", data)
      this.tro = ""
      this.form = {
        addr: "",
        port: 443,
        password: "",
        name: ""
      }
      setTimeout(() => {
        ipc.send("get-nodes")
      }, 1000)
      this.$notify({
        title: "",
        message: `\n${Now()}`,
        type: "success",
        duration: 1500,
        showClose: false
      })
    }
  }
}
</script>
<style scoped>
textarea {
  width: 35%;
  height: 20%;
  min-height: 40px;
}
</style>
